#hamburger {
  #hamburger {
    border-radius: 0.5em;
    top: 1em;
    right: 1em;
    background-color: #777;
    color: $colorlightgrey;
    padding: 0.5em 1em;
    @media #{$media-desktop} {
      display: none;
    }
  }
}
//pokazywanie ukrytego menu
#main-menu {
  display: block;
  @media #{$media-mobile} {
    display: none;
  }
}
.menu__toggle {
  display: block;
  @media #{$media-desktop} {
    display: none;
  }
}

#main-menu-visibility {
  display: none;

  &:checked + #main-menu {
    display: block;
  }
}
.header {
  width: 100%;
  height: 70px;
  position: fixed;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16)
  }


.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 100%;

}

.logo {
  color: $colorblu;
  font-size: 32px;
  font-weight: 100;
  font-family: $font-raleway;
  padding-left: 136px;
  @media #{$media-mobile} {
    top: 0;
    left: 0;
    padding: 2px;
  }
}

  .menu__nav {
    display: flex;
  }

  .menu__el {
    padding: 0 26px;
  }

  .menu__link {
    font-family: $font-open-sans;
    color: $colorgrey;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 28px 18px 22px;
    transition: 0.3s;
    border-bottom: 4px solid transparent;
  }

  .menu__link:hover {
    color: black;
    border-bottom: 4px solid $colorblu;
  }

  .menu__el:last-child {
    padding-right: 0;
  }
