
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero__header {
  margin-top: 130px;
  text-align: center;
}

.hero__title {
  font-family: $font-raleway;
  font-size: 49px;
  color: black;
  font-weight: 400;
  text-align: center;
}

.hero__text {
  font-size: 16px;
  color: $text-color;
  padding-top: 20px;
  padding-bottom: 38px;
  font-family: $font-open-sans;
}

.btn {
  color: white;
  font-weight: bold;
  font-family: $font-open-sans;
  display: inline-block;
  padding: 16px 65px;
  border-radius: 40px;
  border: none;
}

.btn__first {
  background-color: $colorblu;
  border-radius: 40px;
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.16);
}

.btn__hero {

}

.hero__background {
  width: 100%;
  height: auto;
}